import { createComponentFromModel } from 'core/loadEntities';

export default function (entity) {

      if(!entity.get('timeout')) {

        let timeout = {
            "name": "timeout",
            "scope": "common",
            "params": {
                "ending": {
                    "scope": "aJam",
                    "name": "persoFinishEscape"
                },
                "duration": 1000
            }
        }


        const timeoutComponent = createComponentFromModel.call(this, timeout);

        entity.add(timeoutComponent)
      }
}
